    height:80px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: 0px 1px 8px #efbbcc;
    background-color:#E8CEBF;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.nav .rightside{
    -webkit-flex: 65% 1;
            flex: 65% 1;
    /* display: inline; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}
