    .nav h6{
        font-family: 'Rye', cursive;
        font-size:29px;
        font-weight: 600;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .nav{
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
        height: 160px;
    }
    .nav .rightside{
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-flex-direction: 70%;
                flex-direction: 70%;
    }
    .icon{
        /* flex:0.49; */
        -webkit-flex: 30% 1;
                flex: 30% 1;
    }
  }
.categories{
    color: #00695c;
    font-family: 'IM Fell French Canon SC', serif;
    font-weight: 700;
    font-size: larger;
}

.explore{
    color: #4F4846;
    font-family: "Rozha One";
}

.menubutton{
    -webkit-flex: 50% 1;
            flex: 50% 1;
}
.newsCard {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  padding: 10px;
  margin-bottom: 25px;
  border-radius: 5px;
}

.newsText {
  padding: 0 0 0 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.container {
  position: relative;
  max-width: 320px;
  width: 100%;
}
.newsImage {
  object-fit: cover;
  height: 268px;
  width: 320px;
}
.container .btn {
  position: absolute;
  top: 0%;
  left: 0%;
  font-family: "Cormorant Infant";
  background-color: #009688;
  border: none;
  color: white;
  padding: 10px 25px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
.title {
  font-size: 22px;
  line-height: 27px;
  font-family: "Roboto", sans-serif;
}

.author {
  font-size: 12px;
  font-weight: 300;
  line-height: 22px;
  color: #808290;
}

.muted {
  font-weight: 100;
}

.lowerNewsText {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 10px 0;
  position: relative;
}
.description {
  font-size: 16px;
  line-height: 22px;
  color: #44444d;
}
.readmore {
  font-size: 13px;
  font-weight: 300;
  padding-top: 10px;
}
#container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: flex-end;
          align-items: flex-end; /*flex-end*/
  -webkit-justify-content: center;
          justify-content: center;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-left: 70%;
  /* justify-content: flex-end; */
}

#first {
  font-size: 12px;
  font-weight: 500;
  color: #009688;
  padding: 0;
  margin: 0;
}
#second {
  margin-top: 0;
}
.source {
  text-decoration: none;
  color: black;
}

@media (max-width: 800px) {
  .newsCard {
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
  }
  .newsImage {
    width: 100%;
    height: 100%;
    padding-bottom: 20px;
  }
}

.content{
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.downloadMessage{
    width:95%;
    display:-webkit-flex;
    display:flex; /*everything will be aligned in one line*/
    -webkit-align-items: center;
            align-items: center;
    background-color:#E8CEBF;
    height: 48px;
    padding: 0 40px;
    margin: 20px 0 35px 0;
    color: white;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26), 0 2px 10px 0 rgba(0,0,0,0.12);
    border-radius: 4px;
}
.text{
    -webkit-flex:1 1;
            flex:1 1;
    color:#1b1b1b;
    font-weight: 400;
}

@media(max-width: 995px){
    .downloadMessage{
        display: none;
    }
}
.loadMore{
    
    cursor: pointer;
    background-color:#E8CEBF;
    border: none;
    padding: 10px 25px;
    margin-top: -20px;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
}
.loadMore:hover{
    background-color: #009688;
    color: white;
}
.footer{
    background-color: #222;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    color: white;
}

.name{
    font-size: 20px;
    padding: 20px 30px 15px;
    color: white;
}
.iconContainer{
    color: white;
    list-style: none;
    width: 60%;
    height: 50px;
    padding: 20px 0;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around; 
    display: -webkit-flex; 
    display: flex;
}

li{
    
    display: inline;
    padding-right: 40px ;
} 


a:link,
a:hover,
a:visited,
a:active{
  text-decoration: none;
  color:inherit;
}

